<template>
  <div class="student_wrap">
    <div class="header">
      <span class="impot_title">导入学生</span><i class="prop">支持从excel表格批量复制黏贴（一次最多导入150位学生）</i><i class="prop color" @click="showPopup = true">批量添加提示</i>
      <div style="float:right;" v-if="!isUse">
        <span class="impot_title">班级：</span>
        <el-select v-model="classId" filterable placeholder="请选择班级" @change="getList">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
    </div>
    <div class="content" v-if="classId !== null && classId !== undefined && isPower">
      <table class="tableWrap">
        <thead>
          <tr>
            <th>序号</th>
            <th>学生姓名</th>
            <th>性别</th>
            <th>学号</th>
            <th>手机号</th>
            <!-- <th>是否住宿</th> -->
            <th>编辑</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in list" :key="index" @click="tag = index">
            <td>{{index+1}}</td>
            <td>
              <input type="text" class="input_set" @paste="cellkeydown" v-model.trim="item.studentName" :placeholder="index === 0 ? '必填（输入或直接粘贴）' : ''">
            </td>
            <td>
              <input type="text" class="input_set" v-model.trim="item.studentSex" :placeholder="index === 0 ? '选填（男或女）': ''">
            </td>
            <td>
              <input type="text" class="input_set" v-model.trim="item.studentNumber" :placeholder="index === 0 ? '选填': ''">
            </td>
            <td>
              <input type="number" class="input_set" v-model.trim="item.studentTelephone_num" :placeholder="index === 0 ? '选填': ''">
            </td>
            <!-- <td>
              <input type="text" v-model="item.isLodging" :placeholder="index === 0 ? '选填（是或否）': ''">
            </td> -->
            <td class="delete_icon">
              <i v-if="!showDelete" class="el-icon-error change_delete_color" @click="deleteList(index)"></i>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="bottom_button" v-if="classId !== null && classId !== undefined && isPower">
      <span class="impot_button" @click="cleatData" >清空</span>
      <span class="impot_button" @click="addData" >新增</span>
      <span class="impot_button" @click="submitData" >导入</span>
    </div>
    <div v-else class="empty_wrap">
      <div class="prop_center">
        <img src="../../assets/empty.png">
        <p>请先选择班级</p>
      </div>
    </div>
    <popup :showTag="showPopup" :showTaglink="linkFun" :src="require('@/assets/import_student.gif')"></popup>
  </div>
</template>
<script>
import api from './api'
import baseApi from '../api'
import Popup from '../../components/popup'
export default {
  components: { Popup },
  data () {
    return {
      list: [
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        }
      ],
      options: [],
      classList: [],
      classId: null,
      tag: 0,
      showDelete: false,
      showPopup: false,
      isPower: false,
      isUse: false
    }
  },
  mounted () {
    setTimeout(() => {
      if (this.userInfo.isMaster === true && this.userInfo.isTeacher === false) {
        this.isUse = true
        this.$alert('您还不能使用教师功能，请先将自己添加为教师并绑定微信！', '提示', {
          confirmButtonText: '确定'
        })
      } else {
        this.isUse = false
      }
    }, 100)
    baseApi.queryTeacherClassList({schoolId: this.$route.params.schoolId, featureCode: 'XUESHENGXINXI'}, (data) => {
      this.classList = data
      data.forEach((element) => {
        if (parseInt(element.classType) === 1) {
          var item = {
            value: element.classId,
            label: element.className
          }
          this.options.push(item)
        }
      })
    }, (er) => {
      if (er.toUser !== undefined && er.toUser) {
        this.$message.error(er.message)
      }
    })
  },
  watch: {
    list: {
      handler: function (list, oldList) {
        if (list.length <= 1) {
          this.showDelete = true
        } else {
          this.showDelete = false
        }
      },
      deep: true
    }
  },
  computed: {
    isClassMaster () {
      return this.$store.state.isClassMaster
    },
    isManager () {
      return this.$store.state.isManager
    },
    userInfo () {
      return this.$store.state.userInfo
    }
  },
  methods: {
    getList () {
      this.classList.forEach((element) => {
        if (element.classId === parseInt(this.classId)) {
          if (this.userInfo.teacherId === element.classTeacherId || this.isManager) {
            this.isPower = true
          } else {
            this.isPower = false
            this.$message.error('该班级无权限导入')
          }
        }
      })
    },
    linkFun () {
      this.showPopup = false
    },
    addData () {
      this.list.push(
        {
          studentName: '',
          studentSex: '',
          studentNumber: '',
          studentTelephone_num: ''
        }
      )
    },
    submitData () {
      if (this.classId !== undefined && this.classId !== '') {
        var nameList = []
        for (let i = 0; i < this.list.length; i++) {
          if (this.list[i].studentName !== '') {
            nameList.push(this.list[i].studentName)
          }
          if (this.list[i].studentSex !== undefined && this.list[i].studentSex !== null && this.list[i].studentSex !== '') {
            if (this.list[i].studentSex !== '男' && this.list[i].studentSex !== '女') {
              this.$notify.error({
                title: '提示',
                message: '请输入男或女！'
              })
              return
            }
          }
          if (this.list[i].studentTelephone_num) {
            if (!(/^1[34578]\d{9}$/.test(this.list[i].studentTelephone_num))) {
              this.$notify.error({
                title: '提示',
                message: '请填写正确手机号码！'
              })
              return
            }
          }
        }
        this.checkRepeat(nameList)
      }
    },
    checkRepeat (arr) {
      var repeatList = this.removeDuplicatedItem(arr)
      if (repeatList.length > 0) {
        this.$alert('有重复学生姓名: ' + repeatList.join('、') + '。请检查修改后导入！', '提示', {
          confirmButtonText: '确定'
        })
      } else {
        this.importData()
      }
    },
    importData () {
      var finishData = []
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].studentName !== '') {
          var item = {
            studentName: this.list[i].studentName,
            studentSex: this.list[i].studentSex,
            studentNumber: this.list[i].studentNumber,
            studentTelephone_num: this.list[i].studentTelephone_num
          }
          finishData.push(item)
        }
      }
      if (finishData.length > 150) {
        this.$message.error('一次最多导入150位学生')
        return
      }
      api.importStudent({schoolId: this.$route.params.schoolId, classId: this.classId, dataList: finishData}, (data) => {
        this.$alert('导入成功！', '提示', {
          confirmButtonText: '确定'
        })
        this.cleatData()
      }, (er) => {
        if (er.toUser !== undefined && er.toUser) {
          this.$message.error(er.message)
        } else {
          this.$message.error('导入失败')
        }
      })
    },
    removeDuplicatedItem (arr) {
      var newArr = arr.sort()
      var repeatList = []
      var ret = []
      for (var a = 0; a < newArr.length - 1; a++) {
        if (newArr[a] === newArr[a + 1]) {
          repeatList.push(newArr[a])
        }
      }
      for (var i = 0, j = repeatList.length; i < j; i++) {
        if (ret.indexOf(repeatList[i]) === -1) {
          ret.push(repeatList[i])
        }
      }
      return ret
    },
    deleteList (index) {
      this.list.splice(index, 1)
    },
    cleatData () {
      this.list = [
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        },
        {
          studentName: '',
          studentSex: '',
          studentTelephone_num: '',
          studentNumber: '',
          isLodging: ''
        }
      ]
    },
    cellkeydown (event) {
      if (event.type === 'paste') {
        var content = document.querySelector('#textAreaXXDD')
        content.value = ''
        content.focus()
        var inputs = document.querySelectorAll('input')
        for (let i = 0; i < inputs.length; i++) {
          inputs[i].blur()
        }
        setTimeout(() => {
          this.fillData()
        }, 100)
      }
    },
    fillData () {
      var content = document.querySelector('#textAreaXXDD')
      var len = content.value.split('\n')
      var copyData = []
      for (let i = 0; i < len.length; i++) {
        var item = {
          studentName: len[i].split(/\s+/)[0],
          studentSex: len[i].split(/\s+/)[1],
          studentNumber: len[i].split(/\s+/)[2],
          studentTelephone_num: len[i].split(/\s+/)[3]
          // isLodging: len[i].split(/\s+/)[4]
        }
        copyData.push(item)
      }
      if (this.tag !== null && this.tag !== undefined) {
        this.list = this.list.splice(0, this.tag).concat(copyData)
      } else {
        this.list = copyData
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
