  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding:20px;
  box-sizing: border-box;
}
.student_wrap .header{
  background: #fff;
  border-top: 4px solid #67c23a;
  padding: 0 20px;
  box-sizing: border-box;
}
.student_wrap .header .prop{
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  margin-left: 15px;
