  background: #5daf34;
  border-color: #5daf34;
  color: #fff;
}
.student_wrap .content{
  margin-top: 5px;
  background: #fff;
}
.student_wrap .content .tableWrap{
  width: 100%;
  border-spacing: 0;
}
.student_wrap .content .tableWrap thead tr th{
  height: 40px;
  text-align: center;
  background: #F9F9F9;
  vertical-align: middle;
